@media only screen and (max-width: 768px) {
    .a1 {
        background: linear-gradient(180deg, #FFFFFF 30.5%, #72CCE4 100%);
    }

    .div-logo-heder {
        width: 100%;
        background: linear-gradient(0deg, #FFFFFF 30.5%, #72CCE4 100%);
    }

    .div-tital-slogen-img-heder {
        display: flex;
        flex-direction: column;
        justify-content: center;
        justify-items: center;
        align-items: center;
    }

    .tital1-heder {
        /* font-family: Heebo; */
        font-size: 50px;
        font-weight: 300;
        line-height: 78px;
        text-align: center;
        margin-top: 0px;
    }

    .tital2-heder {
        /* font-family: Heebo; */
        font-size: 50px;
        font-weight: 00;
        line-height: 78px;
        text-align: center;
        margin-bottom: 0px;
    }

    .img-slogen-heder-top {
        width: 41px;
        margin-bottom: -45px;
        margin-left: 200px;

    }

    .img-slogen-heder-bottom {
        width: 41px;
        margin-right: 260px;
        margin-bottom: -10px;

    }

    .slogen-heder-txt {
        /* font-family: Heebo; */
        font-size: 16px;
        font-weight: 700;
        line-height: 23.5px;
        text-align: center;
        padding: 0px;

    }

    .slogen-heder-txt1 {
        /* font-family: Heebo; */
        font-size: 16px;
        font-weight: 700;
        line-height: 23.5px;
        text-align: center;
        padding: 0px;
    }

    .div-ConnectAs-heder {
        width: 100%;
    }

    .div-ConnectAs-txt-img-heder {
        display: flex;
        flex-direction: column-reverse;
    }

}

